<template>
  <div>

    <div class="box">
      <p class="has-text-centered">We will send you an email to reset your password.</p>
    <p class="has-text-centered" :style="{color : 'red'}">{{errorMessage}}</p>
        <br />

  
      <form @submit.prevent="validateBeforeSubmit" autocomplete="off">
        <b-field label="Entrez votre adresse email">
          <p class="control has-icon has-icon-right">
            <input
              name="email"
              v-model="email"
              v-validate="'required|email'"
              :class="{'input': true, 'is-danger': errors.has('email') }"
              type="text"
              placeholder="Email"
            />
            <i v-show="errors.has('email')" class="fa fa-warning"></i>
            <span
              v-show="errors.has('email')"
              class="help is-danger"
            >You must enter a valid email</span>
          </p>
        </b-field>

        
        <div class="flex-justify">
        <button type="submit" class="button is-primary">Send</button>
        <p @click="forgotStatus(false)">Cancel </p>
        </div>
      </form>
   
    </div>
  </div>
</template>

<script>
import axiosRequest from "../../axiosRequests";
export default {
    props : ['forgotStatus'],
  data() {
    return {
      email: null,
      errorMessage : null
    };
  },
  methods: {
    validateBeforeSubmit() {
      this.$validator.validateAll().then(result => {
        if (result) {
          this.postMail()
          return;
        }
        this.$buefy.toast.open({
          message: "Form is not valid !",
          type: "is-danger",
          position: "is-bottom"
        });
      });
    },
    postMail() {
      axiosRequest.sendResetMail(this.email)
      .then(response => {
        this.playSuccess(response);
        this.email = null;
      })
      .catch(error => {
        this.errorMessage = error;
        this.email = null;
      })
    },
    playSuccess(val) {
      this.$buefy.toast.open({
        message: val,
        type: "is-success",
        position: "is-bottom",
        duration: 5000
      });
    }
  }
};
</script>

